@font-face {
  font-family: Roboto;
  font-display: swap;
  src: url("/static/fonts/Roboto-Regular.ttf") format("ttf");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: Roboto;
  font-display: swap;
  src: url("/static/fonts/Roboto-Black.ttf") format("ttf");
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: SeymourOne;
  font-display: swap;
  src: url("/static/fonts/SeymourOne-Regular.woff2") format("woff2"), url("/static/fonts/SeymourOne-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
  color: #333;
  font-size: 16px;
  font-family: "SeymourOne";
  font-weight: 900;
}

[class*=__container] {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 15px;
}

.wrapper {
  width: 100%;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.page {
  flex: 1 1 auto;
}

.image {
  width: 100%;
  height: 600px;
  overflow: hidden;
}
.image img {
  width: 100%;
  object-fit: cover;
}

.header__container {
  height: 90px;
  display: flex;
  align-items: center;
}

.navbar__logo {
  font-size: 30px;
  font-weight: 900;
  color: #333;
}